import React, { Fragment } from "react";
// import PropTypes from 'prop-types'
import SectionTitle from "../../../components/section-title";
import { projects } from "./data";
import s from "./projects.module.scss";

const Projects = () => {
  return (
    <div className={s.root}>
      <SectionTitle>Projects</SectionTitle>
      <div className={s.wrap}>
        {projects.map(
          ({
            id,
            title,
            role,
            company,
            description,
            technologies,
            participation,
            tools,
          }) => (
            <div key={id} className={s.item}>
              <h3>{title}</h3>
              <p className={s.grid}>
                <span className={s.key}>Role: </span>
                <span className={s.val}>{`${role} at ${company}`}</span>

                {!!description && (
                  <>
                    <span className={s.key}>Description:</span>
                    <pre className={s.val}>{description}</pre>
                  </>
                )}

                {!!participation?.length && (
                  <>
                    <span className={s.key}>Participation:</span>
                    <ul className={s.list}>
                      {participation.map((p) => (
                        <li key={p}>{p}</li>
                      ))}
                    </ul>
                  </>
                )}

                <span className={s.key}>Technologies:</span>
                <span className={s.val}>
                  {technologies.map((t, i) => (
                    <Fragment key={t}>
                      {t}
                      {technologies.length - 1 > i && ", "}
                    </Fragment>
                  ))}
                </span>

                <span className={s.key}>Technologies:</span>
                <span className={s.val}>
                  {tools.map((t, i) => (
                    <Fragment key={t}>
                      {t}
                      {tools.length - 1 > i && ", "}
                    </Fragment>
                  ))}
                </span>
              </p>
            </div>
          )
        )}
      </div>
    </div>
  );
};

Projects.propTypes = {};

export default Projects;
